import type { Card } from '../entities/Card'
import type { GameStage } from '../types/card.types'
import type { ChallengeResult } from '../types/game.types'
import type { ICardManager } from './CardManager'
import type { Game } from '../entities/Game'
import { RiskRewardChallenge } from '../entities/RiskRewardChallenge'
import { AGE_PARAMETERS, DREAM_AGE_ADJUSTMENTS } from '../types/game.types'
import { MAX_TOTAL_DAMAGE_REDUCTION, MINIMUM_DAMAGE_AFTER_INSURANCE } from '../constants/insurance.constants'
import { GameConstantsAccessor } from '../constants/GameConstants'

/**
 * ãƒãƒ£ãƒ¬ãƒ³ã‚¸è§£æ±ºã‚µãƒ¼ãƒ“ã‚¹
 * 
 * ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã®è§£æ±ºãƒ­ã‚¸ãƒƒã‚¯ã‚’å°‚é–€ã«æ‰±ã†å˜ä¸€è²¬ä»»ã‚¯ãƒ©ã‚¹
 */
export class ChallengeResolutionService {
  /**
   * ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã‚’è§£æ±ºã—ã€çµæœã‚’è¨ˆç®—
   * @param challenge ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã‚«ãƒ¼ãƒ‰
   * @param selectedCards é¸æŠã•ã‚ŒãŸã‚«ãƒ¼ãƒ‰
   * @param cardManager ã‚«ãƒ¼ãƒ‰ç®¡ç†ã‚µãƒ¼ãƒ“ã‚¹
   * @param stage ç¾åœ¨ã®ã‚²ãƒ¼ãƒ ã‚¹ãƒ†ãƒ¼ã‚¸
   * @param insuranceBurden ä¿é™ºæ–™è² æ‹…
   * @param game ã‚²ãƒ¼ãƒ ã‚¨ãƒ³ãƒ†ã‚£ãƒ†ã‚£ï¼ˆä¿é™ºæƒ…å ±å–å¾—ç”¨ï¼‰
   * @returns ãƒãƒ£ãƒ¬ãƒ³ã‚¸çµæœ
   */
  resolveChallenge(
    challenge: Card,
    selectedCards: Card[],
    cardManager: ICardManager,
    stage: GameStage,
    insuranceBurden: number,
    game?: Game
  ): ChallengeResult {
    // ãƒªã‚¹ã‚¯ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã®ç‰¹æ®Šãƒ«ãƒ¼ãƒ«ç¢ºèª
    const isRiskChallenge = challenge instanceof RiskRewardChallenge
    const insuranceImmunity = isRiskChallenge && challenge.insuranceImmunity

    // ä¿é™ºåŠ¹æœï¼ˆç‰¹åŒ–å‹ãƒœãƒ¼ãƒŠã‚¹ï¼‰
    const insuranceBonus = (game && !insuranceImmunity) ? this.calculateInsuranceBonus(game, challenge) : 0

    // ãƒ‘ãƒ¯ãƒ¼è¨ˆç®—
    const powerBreakdown = this.calculateTotalPower(selectedCards, insuranceBurden, insuranceBonus)
    const playerPower = powerBreakdown.total

    // å¤¢ã‚«ãƒ¼ãƒ‰ã®å¹´é½¢èª¿æ•´
    const challengePower = this.getDreamRequiredPower(challenge, stage)

    // æˆåŠŸåˆ¤å®š
    const success = playerPower >= challengePower

    let vitalityChange = 0
    let message = ''
    let damageAmount: number | undefined

    if (success) {
      // æˆåŠŸæ™‚: åŸºæœ¬å ±é…¬ + éåŠ´ãƒ€ãƒ¡ãƒ¼ã‚¸è¨ˆç®—
      const bonusBase = GameConstantsAccessor.getBalanceSettings().CHALLENGE_SETTINGS.successBonusBase
      const powerDiff = playerPower - challengePower

      // V3å¤‰æ›´: ã€Œæƒœã—ã„ã€åˆ¤å®šå»ƒæ­¢ã®ãŸã‚ã€éå‰°ãƒ‘ãƒ¯ãƒ¼ã¯ã€ŒéåŠ´ã€ã¨ã—ã¦ãƒšãƒŠãƒ«ãƒ†ã‚£
      // å ±é…¬ã¯å›ºå®šï¼ˆ+5ï¼‰ãªã©ã«ã™ã‚‹ã‹ã€å·®åˆ†ãƒœãƒ¼ãƒŠã‚¹ã‚’æ®‹ã™ã‹ï¼Ÿ
      // ãƒ¦ãƒ¼ã‚¶ãƒ¼è¦æœ›ã€Œé”æˆã§ãã¦ã‚‚è¶…éã—ã¦ã„ã‚Œã°è¶…éåˆ†/5ã®åˆ‡ã‚Šæ¨ã¦ã‚’å—ã‘ã‚‹ã€
      // å ±é…¬è¨ˆç®—ã¯å¾“æ¥ã®ã€ŒåŸºæœ¬+å·®åˆ†/2ã€ã ã¨éåŠ´ã¨ç›¸æ®ºã—ã¦ãƒ—ãƒ©ã‚¹ã«ãªã‚‹å¯èƒ½æ€§ãŒé«˜ã„ã€‚
      // ã“ã“ã¯ã‚·ãƒ³ãƒ—ãƒ«ã«ã€ŒåŸºæœ¬å ±é…¬ã®ã¿ã€ã«ã—ã¦ã€éåŠ´ã‚’å¼•ãå½¢ãŒç¾ã—ã„ã‹ï¼Ÿ
      // ã„ã£ãŸã‚“ã€ŒåŸºæœ¬å ±é…¬ã€ã®ã¿ã«ã—ã¾ã™ã€‚
      const baseReward = bonusBase // å·®åˆ†ãƒœãƒ¼ãƒŠã‚¹æ’¤å»ƒ

      // éåŠ´ãƒ€ãƒ¡ãƒ¼ã‚¸è¨ˆç®—
      // V3.1 Balance: Increase overwork penalty to punish inefficient play (Beginner).
      // Overflow / 2 (was 5).
      const overworkDamage = Math.floor(powerDiff / 2)

      // éåŠ´ãƒ€ãƒ¡ãƒ¼ã‚¸ã‚‚ä¿é™ºã§è»½æ¸›å¯èƒ½ï¼ˆåŒ»ç™‚ä¿é™ºãªã©ï¼‰
      const damageReduction = (game && !insuranceImmunity) ? this.calculateDamageReduction(game) : 0
      const finalOverworkDamage = Math.max(0, overworkDamage - damageReduction)

      vitalityChange = baseReward - finalOverworkDamage

      message = `ğŸ‰ ãƒãƒ£ãƒ¬ãƒ³ã‚¸æˆåŠŸï¼ (+${baseReward})`
      if (finalOverworkDamage > 0) {
        message += ` ã—ã‹ã—é ‘å¼µã‚Šã™ãã¦ç–²ã‚ŒãŸ... (-${finalOverworkDamage})`
      } else if (overworkDamage > 0 && finalOverworkDamage === 0) {
        message += ` (ä¿é™ºãŒéåŠ´ã‚’é˜²ã„ã ï¼)`
      }
    } else {
      // å¤±æ•—æ™‚: V3ãƒ«ãƒ¼ãƒ«ã€Œä¸è¶³åˆ†é–¢ä¿‚ãªãã€å³ä¸Šã®å€¤ã‚’ä¸¸ã”ã¨å—ã‘ã‚‹ã€
      // å›ºå®šãƒ€ãƒ¡ãƒ¼ã‚¸ = ãƒãƒ£ãƒ¬ãƒ³ã‚¸ãƒ‘ãƒ¯ãƒ¼
      const baseDamage = challengePower

      // ä¿é™ºè»½æ¸›
      const damageReduction = (game && !insuranceImmunity) ? this.calculateDamageReduction(game) : 0

      // æœ€å°ãƒ€ãƒ¡ãƒ¼ã‚¸ä¿è¨¼ï¼ˆã©ã‚“ãªã«è»½æ¸›ã—ã¦ã‚‚1ã¯é£Ÿã‚‰ã†ã€ãŸã ã—å®Œå…¨ç„¡åŠ¹åŒ–ã‚¹ã‚­ãƒ«ãŒã‚ã‚Œã°åˆ¥ã ãŒç¾çŠ¶ã¯1ï¼‰
      const finalDamage = Math.max(MINIMUM_DAMAGE_AFTER_INSURANCE, baseDamage - damageReduction)

      // å¤¢ï¼ˆå¤§ãƒ€ãƒ¡ãƒ¼ã‚¸ï¼‰ã«å¯¾ã™ã‚‹ç‰¹æ®Šé˜²å¾¡ï¼ˆãŒã‚“ä¿é™ºãªã©ï¼‰ã®ãƒ­ã‚¸ãƒƒã‚¯ã¯
      // calculateDamageReductionã«å«ã‚ã‚‹ã‹ã€ã“ã“ã§åˆ¥é€”åˆ¤å®šã™ã‚‹ã‹ï¼Ÿ
      // ç¾çŠ¶ã®Cardãƒ­ã‚¸ãƒƒã‚¯ã§ã¯ã€Œè»½æ¸›é‡ã€ã‚’è¿”ã™ã®ã§ã€
      // 20ä»¥ä¸Šã®ã¨ãã«è»½æ¸›é‡ã‚’20ã«ã™ã‚‹ã€ãªã©ã®å®Ÿè£…ãŒCardå´ã§å¿…è¦ã€‚
      // ã„ã£ãŸã‚“ãã®ã¾ã¾è¨ˆç®—ã€‚

      vitalityChange = -finalDamage
      damageAmount = finalDamage
      message = `ğŸ’¥ å¤±æ•—... ${finalDamage} ã®ãƒ€ãƒ¡ãƒ¼ã‚¸ã‚’å—ã‘ãŸ`
    }

    // ã‚«ãƒ¼ãƒ‰ç ´æ£„
    cardManager.discardSelectedCards()

    // ãƒªã‚¹ã‚¯ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã®å ´åˆã®è£œæ­£ï¼ˆã‚‚ã—ã‚ã‚Œã°ï¼‰
    if (isRiskChallenge) {
      // ãƒªã‚¹ã‚¯ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã®è¨ˆç®—ãƒ­ã‚¸ãƒƒã‚¯ã‚’å„ªå…ˆã™ã‚‹å ´åˆã“ã“ã«æˆ»ã™å¿…è¦ã‚ã‚Š
      // ç¾çŠ¶ã¯V3ãƒ«ãƒ¼ãƒ«å„ªå…ˆ
    }

    // çµæœä½œæˆ
    const result: ChallengeResult = {
      challenge,
      success,
      resultType: success ? 'success' : 'damage_taken',
      playerPower,
      challengePower,
      vitalityChange,
      message,
      powerBreakdown
    }

    // ãƒ€ãƒ¡ãƒ¼ã‚¸é‡ã¯ damage_taken æ™‚ã®ã¿è¨­å®š
    if (!success && damageAmount !== undefined) {
      result.damageAmount = damageAmount
    }

    return result
  }

  /**
   * ç·åˆãƒ‘ãƒ¯ãƒ¼ã‚’è©³ç´°ã«è¨ˆç®—
   * @param cards ä½¿ç”¨ã™ã‚‹ã‚«ãƒ¼ãƒ‰
   * @param insuranceBurden ä¿é™ºæ–™è² æ‹…
   * @param insuranceBonus ä¿é™ºãƒœãƒ¼ãƒŠã‚¹
   * @returns ãƒ‘ãƒ¯ãƒ¼ã®è©³ç´°ãªå†…è¨³
   */
  private calculateTotalPower(cards: Card[], insuranceBurden: number, insuranceBonus: number = 0): {
    base: number
    insurance: number
    burden: number
    total: number
  } {
    // åŸºæœ¬ãƒ‘ãƒ¯ãƒ¼ï¼ˆä¿é™ºä»¥å¤–ã®ã‚«ãƒ¼ãƒ‰ï¼‰
    let basePower = 0
    let insurancePower = 0

    cards.forEach(card => {
      if (card.type === 'insurance') {
        // ä¿é™ºã‚«ãƒ¼ãƒ‰ã®ãƒ‘ãƒ¯ãƒ¼ï¼ˆå¹´é½¢ãƒœãƒ¼ãƒŠã‚¹è¾¼ã¿ï¼‰
        insurancePower += card.calculateEffectivePower()
      } else {
        // ãã®ä»–ã®ã‚«ãƒ¼ãƒ‰ã®åŸºæœ¬ãƒ‘ãƒ¯ãƒ¼
        basePower += card.calculateEffectivePower()
      }
    })

    // ä¿é™ºãƒœãƒ¼ãƒŠã‚¹ã‚’ä¿é™ºãƒ‘ãƒ¯ãƒ¼ã«åŠ ç®—
    insurancePower += insuranceBonus

    // ç·åˆãƒ‘ãƒ¯ãƒ¼
    const total = basePower + insurancePower - insuranceBurden

    return {
      base: basePower,
      insurance: insurancePower,
      burden: -insuranceBurden, // è² ã®å€¤ã¨ã—ã¦è¡¨ç¤º
      total: Math.max(0, total) // ç·åˆãƒ‘ãƒ¯ãƒ¼ã¯0ä»¥ä¸‹ã«ãªã‚‰ãªã„
    }
  }

  /**
   * å¤¢ã‚«ãƒ¼ãƒ‰ã®å¿…è¦ãƒ‘ãƒ¯ãƒ¼ã‚’å¹´é½¢èª¿æ•´è¾¼ã¿ã§è¨ˆç®—
   */
  private getDreamRequiredPower(challenge: Card, stage: GameStage): number {
    // å¤¢ã‚«ãƒ¼ãƒ‰ã§ãªã„å ´åˆã¯åŸºæœ¬ãƒ‘ãƒ¯ãƒ¼ã‚’ãã®ã¾ã¾è¿”ã™
    // NOTE: é€šå¸¸ã®ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã§ã‚‚dreamCategoryï¼ˆèº«ä½“çš„/çŸ¥è­˜çš„ï¼‰ãŒè¨­å®šã•ã‚Œã¦ã„ã‚Œã°å¹´é½¢èª¿æ•´ã‚’é©ç”¨ã™ã‚‹
    if (!challenge.dreamCategory) {
      return challenge.power
    }

    // é’å¹´æœŸã¯èª¿æ•´ãªã—
    if (stage === 'youth') {
      return challenge.power
    }

    // ä¸­å¹´æœŸãƒ»å……å®ŸæœŸã®å¹´é½¢èª¿æ•´ã‚’é©ç”¨ï¼ˆGAME_DESIGN.mdæº–æ‹ ï¼‰
    // Physical: +3, Intellectual: -2, Mixed: 0
    // @ts-ignore
    const adjustment = DREAM_AGE_ADJUSTMENTS[challenge.dreamCategory] || 0
    const adjustedPower = challenge.power + adjustment

    // æœ€å°å€¤ã¯1
    return Math.max(1, adjustedPower)
  }

  /**
   * ä¿é™ºã«ã‚ˆã‚‹ãƒãƒ£ãƒ¬ãƒ³ã‚¸ãƒœãƒ¼ãƒŠã‚¹ã‚’è¨ˆç®—
   * @private
   */
  private calculateInsuranceBonus(game: Game, challenge: Card): number {
    let totalBonus = 0
    const insuranceCards = game.getActiveInsurances()
    const currentStage = game.stage
    const ageParams = AGE_PARAMETERS[currentStage] || AGE_PARAMETERS['youth']
    const ageMultiplier = ageParams?.ageMultiplier ?? 0

    insuranceCards.forEach(insurance => {
      // çµ‚èº«ä¿é™ºã®å¹´é½¢ä¾¡å€¤ä¸Šæ˜‡ã‚’é©ç”¨
      let bonus = 0

      if (insurance.isSpecializedInsurance()) {
        const challengeType = challenge.name
        bonus = insurance.calculateChallengeBonus(challengeType)
      }

      // çµ‚èº«ä¿é™ºã®å ´åˆã€å¹´é½¢å€ç‡ã‚’é©ç”¨
      if (insurance.isWholeLifeInsurance() && ageMultiplier > 0) {
        bonus += ageMultiplier  // ä¸­å¹´æœŸ+0.5ã€å……å®ŸæœŸ+1.0
      }

      totalBonus += bonus
    })

    return totalBonus
  }

  /**
   * é˜²å¾¡å‹ä¿é™ºã«ã‚ˆã‚‹ãƒ€ãƒ¡ãƒ¼ã‚¸è»½æ¸›ã‚’è¨ˆç®—
   * Issue #24: è¤‡æ•°ä¿é™ºã®åˆè¨ˆè»½æ¸›é‡ã«ã‚‚ä¸Šé™ã‚’è¨­å®š
   * @private
   */
  private calculateDamageReduction(game: Game): number {
    let totalReduction = 0
    const insuranceCards = game.getActiveInsurances()

    insuranceCards.forEach(insurance => {
      // calculateDamageReductionå†…ã§é˜²å¾¡åŠ¹æœã®ãƒã‚§ãƒƒã‚¯ã‚’è¡Œã†ãŸã‚ã€
      // å…¨ã¦ã®ä¿é™ºã‚«ãƒ¼ãƒ‰ã§è¨ˆç®—ã‚’å®Ÿè¡Œ
      totalReduction += insurance.calculateDamageReduction()
    })

    // åˆè¨ˆè»½æ¸›é‡ã®ä¸Šé™ã‚’é©ç”¨
    return Math.min(totalReduction, MAX_TOTAL_DAMAGE_REDUCTION)
  }
}