/**
 * ã‚«ãƒ¼ãƒ‰ã®ãƒ‘ãƒ¯ãƒ¼ã‚’è¡¨ã™å€¤ã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆ
 * 
 * ã“ã®ã‚¯ãƒ©ã‚¹ã¯ã‚¤ãƒŸãƒ¥ãƒ¼ã‚¿ãƒ–ãƒ«ã§ã‚ã‚Šã€ã™ã¹ã¦ã®æ“ä½œã¯æ–°ã—ã„ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’è¿”ã—ã¾ã™ã€‚
 * ãƒ“ã‚¸ãƒã‚¹ãƒ«ãƒ¼ãƒ«ï¼š
 * - ãƒ‘ãƒ¯ãƒ¼ã¯-99ä»¥ä¸Šã€999ä»¥ä¸‹ã§ãªã‘ã‚Œã°ãªã‚‰ãªã„ï¼ˆãƒã‚¬ãƒ†ã‚£ãƒ–ãªäººç”Ÿã‚¤ãƒ™ãƒ³ãƒˆå¯¾å¿œï¼‰
 * - æ¼”ç®—çµæœãŒç¯„å›²ã‚’è¶…ãˆã‚‹å ´åˆã¯ç¯„å›²å†…ã«åˆ¶é™ã•ã‚Œã‚‹
 */
export class CardPower {
  private static readonly MIN_POWER = -99
  private static readonly MAX_POWER = 999

  private constructor(private readonly value: number) {
    this.validate()
  }

  /**
   * CardPower ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’ç”Ÿæˆã™ã‚‹
   * @param value ãƒ‘ãƒ¯ãƒ¼å€¤
   * @throws {Error} ä¸æ­£ãªå€¤ã®å ´åˆ
   */
  static create(value: number): CardPower {
    return new CardPower(value)
  }

  /**
   * å€¤ã®å¦¥å½“æ€§ã‚’æ¤œè¨¼ã™ã‚‹
   * @private
   */
  private validate(): void {
    if (this.value < CardPower.MIN_POWER) {
      throw new Error(`CardPower must be at least ${CardPower.MIN_POWER}`)
    }
    if (this.value > CardPower.MAX_POWER) {
      throw new Error('Card power cannot exceed maximum')
    }
  }

  /**
   * ãƒ‘ãƒ¯ãƒ¼å€¤ã‚’å–å¾—
   */
  getValue(): number {
    return this.value
  }

  /**
   * ä»–ã®CardPowerã¨åŠ ç®—
   * @param other åŠ ç®—ã™ã‚‹CardPower
   * @returns æ–°ã—ã„CardPowerã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹
   */
  add(other: CardPower): CardPower {
    const sum = this.value + other.value
    return new CardPower(Math.max(CardPower.MIN_POWER, Math.min(sum, CardPower.MAX_POWER)))
  }

  /**
   * è¤‡æ•°ã®CardPowerã‚’åˆè¨ˆ
   * @param powers CardPowerã®é…åˆ—
   * @returns åˆè¨ˆã®CardPower
   */
  static sum(powers: CardPower[]): CardPower {
    const total = powers.reduce((sum, power) => sum + power.value, 0)
    return new CardPower(Math.max(CardPower.MIN_POWER, Math.min(total, CardPower.MAX_POWER)))
  }

  /**
   * å€ç‡ã‚’é©ç”¨
   * @param multiplier å€ç‡ï¼ˆ0ä»¥ä¸Šï¼‰
   * @returns æ–°ã—ã„CardPowerã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹
   * @throws {Error} å€ç‡ãŒè² ã®å ´åˆ
   */
  multiply(multiplier: number): CardPower {
    if (multiplier < 0) {
      throw new Error('Multiplier cannot be negative')
    }
    const result = Math.floor(this.value * multiplier)
    return new CardPower(Math.max(CardPower.MIN_POWER, Math.min(result, CardPower.MAX_POWER)))
  }

  /**
   * ä»–ã®CardPowerã‚ˆã‚Šå¤§ãã„ã‹åˆ¤å®š
   */
  isGreaterThan(other: CardPower): boolean {
    return this.value > other.value
  }

  /**
   * ä»–ã®CardPowerä»¥ä¸Šã‹åˆ¤å®š
   */
  isGreaterThanOrEqual(other: CardPower): boolean {
    return this.value >= other.value
  }

  /**
   * ä»–ã®CardPowerã¨ç­‰ä¾¡ã‹åˆ¤å®š
   */
  equals(other: CardPower): boolean {
    return this.value === other.value
  }

  /**
   * æ–‡å­—åˆ—è¡¨ç¾ã‚’å–å¾—
   */
  toString(): string {
    return `Power: ${this.value}`
  }

  /**
   * ã‚¼ãƒ­ãƒ‘ãƒ¯ãƒ¼ã®å®šæ•°
   */
  static get ZERO(): CardPower {
    return new CardPower(0)
  }

  /**
   * æœ€å¤§ãƒ‘ãƒ¯ãƒ¼ã®å®šæ•°
   */
  static get MAX(): CardPower {
    return new CardPower(CardPower.MAX_POWER)
  }
}