/**
 * ã‚²ãƒ¼ãƒ ãƒãƒ£ãƒ¬ãƒ³ã‚¸å‡¦ç†ã‚µãƒ¼ãƒ“ã‚¹
 * 
 * ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã®è§£æ±ºã«é–¢ã™ã‚‹è¤‡é›‘ãªãƒ­ã‚¸ãƒƒã‚¯ã‚’ç®¡ç†
 * 
 * @class GameChallengeService
 * @description
 * ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã®é–‹å§‹ã‹ã‚‰è§£æ±ºã¾ã§ã®ä¸€é€£ã®ãƒ—ãƒ­ã‚»ã‚¹ã‚’ç®¡ç†ã—ã¾ã™ã€‚
 * ãƒ‘ãƒ¯ãƒ¼è¨ˆç®—ã€æˆåŠŸåˆ¤å®šã€æ´»åŠ›å¤‰æ›´ã€çµ±è¨ˆæ›´æ–°ãªã©ã‚’æ‹…å½“ã—ã¾ã™ã€‚
 * 
 * @example
 * ```typescript
 * const challengeService = new GameChallengeService(resolutionService);
 * 
 * // ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã‚’é–‹å§‹
 * challengeService.startChallenge(game, challengeCard);
 * 
 * // ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã‚’è§£æ±º
 * const result = challengeService.resolveChallenge(game);
 * if (result.success) {
 *   console.log('ãƒãƒ£ãƒ¬ãƒ³ã‚¸æˆåŠŸï¼');
 * }
 * ```
 */

import type { Card } from '../entities/Card'
import type { Game } from '../entities/Game'
import type { ChallengeResult, InsuranceTypeChoice } from '../types/game.types'
import { CardFactory } from './CardFactory'
import type { ChallengeResolutionService } from './ChallengeResolutionService'

/**
 * ãƒ‘ãƒ¯ãƒ¼è¨ˆç®—ã®å†…è¨³
 * 
 * @interface PowerBreakdown
 * @property {number} base - åŸºæœ¬ã‚«ãƒ¼ãƒ‰ã®ãƒ‘ãƒ¯ãƒ¼åˆè¨ˆ
 * @property {number} insurance - ä¿é™ºã‚«ãƒ¼ãƒ‰ã®ãƒ‘ãƒ¯ãƒ¼åˆè¨ˆ
 * @property {number} burden - ä¿é™ºæ–™è² æ‹…ï¼ˆè² ã®å€¤ï¼‰
 * @property {number} total - ç·åˆãƒ‘ãƒ¯ãƒ¼ï¼ˆæœ€å°å€¤ã¯0ï¼‰
 */
export interface PowerBreakdown {
  base: number
  insurance: number
  burden: number
  total: number
}

export class GameChallengeService {
  constructor(
    private readonly resolutionService: ChallengeResolutionService
  ) { }

  /**
   * ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã‚’é–‹å§‹
   */
  startChallenge(game: Game, challengeCard: Card): void {
    this.validatePhase(game, 'draw')

    game.currentChallenge = challengeCard
    game.cardManager.clearSelection()
    game.phase = 'challenge'

    // çµŒé¨“å­¦ç¿’ã‚·ã‚¹ãƒ†ãƒ : åŒã˜ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã®å¤±æ•—å›æ•°ã‚’å–å¾—
    const failureCount = game.getLearningHistory(challengeCard.name)
    if (failureCount >= 2) {
      // 2å›ç›®ä»¥é™ã®å¤±æ•—ã§å¿…è¦ãƒ‘ãƒ¯ãƒ¼-1ï¼ˆçµŒé¨“ã«ã‚ˆã‚‹åŠ¹ç‡åŒ–ï¼‰
      // Card is immutable, so we create a copy with updated power
      const newPower = Math.max(1, challengeCard.power - 1)
      const updatedCard = challengeCard.copy({ power: newPower })
      game.currentChallenge = updatedCard
    }
  }

  /**
   * ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã‚’è§£æ±º
   */
  resolveChallenge(game: Game): ChallengeResult {
    try {
      this.validateChallenge(game)

      // æ–°ã—ã„ChallengeResolutionServiceã‚’ä½¿ç”¨
      const result = this.resolutionService.resolveChallenge(
        game.currentChallenge!,
        game.selectedCards,
        game.cardManager,
        game.stage,
        game.insuranceBurden,
        game
      )

      // çµ±è¨ˆæ›´æ–°
      this.updateStatistics(game, result.success)

      // æ´»åŠ›æ›´æ–°
      this.updateVitality(game, result.vitalityChange)

      // çµŒé¨“å­¦ç¿’ã‚·ã‚¹ãƒ†ãƒ : å¤±æ•—æ™‚ã«å­¦ç¿’å±¥æ­´ã‚’æ›´æ–°
      if (!result.success && game.currentChallenge) {
        const challengeName = game.currentChallenge.name
        const currentFailures = game.getLearningHistory(challengeName)
        game.updateLearningHistory(challengeName, currentFailures + 1)

        // å¤¢ã‚«ãƒ¼ãƒ‰ã«æ•—åŒ—ã—ãŸå ´åˆã®ãƒšãƒŠãƒ«ãƒ†ã‚£ï¼ˆé›£æ˜“åº¦ä¸Šæ˜‡ï¼‰
        if (game.currentChallenge.isDreamCard()) {
          console.log(`[Game] Defeated by dream: ${challengeName}. Increasing difficulty.`)
          game.challengeDifficultyModifier += 2
          console.log(`[Game] Difficulty modifier increased to ${game.challengeDifficultyModifier}`)
        }
      }

      // æˆåŠŸæ™‚: ä¿é™ºé¸æŠè‚¢ã‚’æç¤º
      if (result.success) {
        // å¤¢ã‚«ãƒ¼ãƒ‰é”æˆãƒã‚§ãƒƒã‚¯
        if (game.currentChallenge?.type === 'dream' || (game.currentChallenge as any).isDream) {
          console.log('[GameChallengeService] Dream fulfilled! Victory!');
          game.finishGame(true);
          // å‹åˆ©æ™‚ã¯ä¿é™ºé¸æŠã‚’ã‚¹ã‚­ãƒƒãƒ—ã—ã¦å³ãƒªã‚¿ãƒ¼ãƒ³
          this.updateGameStateAfterChallenge(game, result);
          return result;
        }

        const choices = CardFactory.createInsuranceTypeChoices(game.stage)
        game.insuranceTypeChoices = choices
        console.log('[GameChallengeService] Generated insurance choices:', game.insuranceTypeChoices?.length)

        if (!game.insuranceTypeChoices || game.insuranceTypeChoices.length === 0) {
          console.warn('[GameChallengeService] WARNING: No insurance choices generated!')
        }

        // Phase transition is handled by calling context or Game logic
        result.insuranceTypeChoices = choices

        // User Request: Omit Reward Cards explicitly.
        // Reward generation logic has been removed to prevent deck bloat ("ãƒ‘ãƒ¯ã‚«" issue)
      }

      this.updateGameStateAfterChallenge(game, result)

      return result
    } catch (error) {
      console.error('[GameChallengeService] Fatal error resolving challenge:', error)
      // ã‚¨ãƒ©ãƒ¼æ™‚ã®ãƒ•ã‚©ãƒ¼ãƒ«ãƒãƒƒã‚¯çµæœã‚’è¿”ã™
      return {
        challenge: game.currentChallenge || CardFactory.createCard({
          base: { name: 'Error', type: 'life', description: 'System Error' },
          variant: 'default'
        }),
        success: false,
        playerPower: 0,
        challengePower: 0,
        vitalityChange: 0,
        message: `ã‚·ã‚¹ãƒ†ãƒ ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ: ${error instanceof Error ? error.message : String(error)}`,
        resultType: 'error' // ã‚¨ãƒ©ãƒ¼æ‰±ã„
      }
    }
  }

  /**
   * ç·åˆãƒ‘ãƒ¯ãƒ¼ã‚’è¨ˆç®—
   * 
   * @method calculateTotalPower
   * @param {Game} game - ã‚²ãƒ¼ãƒ ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹
   * @param {Card[]} cards - è¨ˆç®—å¯¾è±¡ã®ã‚«ãƒ¼ãƒ‰é…åˆ—
   * @returns {PowerBreakdown} ãƒ‘ãƒ¯ãƒ¼ã®è©³ç´°ãªå†…è¨³
   * 
   * @description
   * é¸æŠã•ã‚ŒãŸã‚«ãƒ¼ãƒ‰ã®ãƒ‘ãƒ¯ãƒ¼ã‚’è¨ˆç®—ã—ã€ä¿é™ºæ–™è² æ‹…ã‚’è€ƒæ…®ã—ãŸ
   * ç·åˆãƒ‘ãƒ¯ãƒ¼ã‚’ç®—å‡ºã—ã¾ã™ã€‚çµæœã¯å¸¸ã«0ä»¥ä¸Šã«ãªã‚Šã¾ã™ã€‚
   */
  calculateTotalPower(game: Game, cards: Card[]): PowerBreakdown {
    let basePower = 0
    let insurancePower = 0

    for (const card of cards) {
      if (card.type === 'insurance') {
        insurancePower += card.calculateEffectivePower()
      } else {
        basePower += card.calculateEffectivePower()
      }
    }

    const burden = game.insuranceBurden
    const total = Math.max(0, basePower + insurancePower + burden)

    return { base: basePower, insurance: insurancePower, burden, total }
  }

  /**
   * ãƒãƒªãƒ‡ãƒ¼ã‚·ãƒ§ãƒ³: ãƒ•ã‚§ãƒ¼ã‚ºãƒã‚§ãƒƒã‚¯
   * @private
   */
  private validatePhase(game: Game, expectedPhase: string): void {
    // v2: allow challenge_choice phase
    if (game.phase === 'challenge_choice' && expectedPhase === 'draw') {
      return
    }

    if (game.phase !== expectedPhase) {
      if (expectedPhase === 'draw') {
        throw new Error(`Can only start challenge during draw or challenge_choice phase (Current: ${game.phase})`)
      }
      throw new Error(`Can only perform this action during ${expectedPhase} phase (Current: ${game.phase})`)
    }
  }

  /**
   * ãƒãƒªãƒ‡ãƒ¼ã‚·ãƒ§ãƒ³: ãƒãƒ£ãƒ¬ãƒ³ã‚¸å­˜åœ¨ãƒã‚§ãƒƒã‚¯
   * @private
   */
  private validateChallenge(game: Game): void {
    if (!game.currentChallenge || game.phase !== 'challenge') {
      throw new Error('No active challenge to resolve')
    }
  }

  /**
   * çµ±è¨ˆã‚’æ›´æ–°
   * @private
   */
  private updateStatistics(game: Game, success: boolean): void {
    game.stats.totalChallenges++
    if (success) {
      game.stats.successfulChallenges++
      // challengesCompletedã‚‚æ›´æ–°ï¼ˆãƒ†ã‚¹ãƒˆã§ã®æœŸå¾…å€¤å¯¾å¿œï¼‰
      if (!game.stats.challengesCompleted) {
        game.stats.challengesCompleted = 0
      }
      game.stats.challengesCompleted++
    } else {
      game.stats.failedChallenges++
      // challengesFailedã‚‚æ›´æ–°ï¼ˆçµ±è¨ˆã®æ•´åˆæ€§ç¢ºä¿ï¼‰
      if (!game.stats.challengesFailed) {
        game.stats.challengesFailed = 0
      }
      game.stats.challengesFailed++
    }
  }

  /**
   * æ´»åŠ›ã‚’æ›´æ–°
   * @private
   */
  private updateVitality(game: Game, change: number): void {
    if (change >= 0) {
      game.heal(change)
    } else {
      const damage = -change

      // åŒ»ç™‚ä¿é™ºãƒˆãƒªã‚¬ãƒ¼ãƒã‚§ãƒƒã‚¯ (ãƒ€ãƒ¡ãƒ¼ã‚¸10ä»¥ä¸Š)
      if (damage >= 10) {
        const insurance = game.activeInsurances.find(c => c.insuranceTriggerType === 'on_heavy_damage')
        if (insurance) {
          game.triggerInsuranceClaim(insurance, 'on_heavy_damage')
          if (game.pendingInsuranceClaim) {
            game.pendingInsuranceClaim.context = { damage }
            return // ãƒ€ãƒ¡ãƒ¼ã‚¸é©ç”¨ã‚’ä¿ç•™
          }
        }
      }

      game.applyDamage(damage)
    }
  }

  /**
   * ãƒãƒ£ãƒ¬ãƒ³ã‚¸å¾Œã®ã‚²ãƒ¼ãƒ çŠ¶æ…‹æ›´æ–°
   * @private
   */
  private updateGameStateAfterChallenge(
    game: Game,
    result: ChallengeResult
  ): void {
    // ä½¿ç”¨ã—ãŸã‚«ãƒ¼ãƒ‰ã‚’æ¨ã¦æœ­ã«
    game.cardManager.discardSelectedCards()

    // ãƒ•ã‚§ãƒ¼ã‚ºæ›´æ–°
    game.phase = result.success
      ? 'insurance_type_selection'
      : 'resolution'

    // ãƒãƒ£ãƒ¬ãƒ³ã‚¸ã‚’ã‚¯ãƒªã‚¢
    game.currentChallenge = undefined
    game.cardManager.clearSelection()
  }
}