/**
 * æ´»åŠ›ã‚’è¡¨ã™å€¤ã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆ
 * 
 * ã“ã®ã‚¯ãƒ©ã‚¹ã¯ã‚¤ãƒŸãƒ¥ãƒ¼ã‚¿ãƒ–ãƒ«ã§ã‚ã‚Šã€ã™ã¹ã¦ã®æ“ä½œã¯æ–°ã—ã„ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’è¿”ã—ã¾ã™ã€‚
 * ãƒ“ã‚¸ãƒã‚¹ãƒ«ãƒ¼ãƒ«ï¼š
 * - æ´»åŠ›ã¯0ä»¥ä¸Š100ä»¥ä¸‹ã§ãªã‘ã‚Œã°ãªã‚‰ãªã„
 */
export class Vitality {
  private static readonly DEFAULT_MAX_VITALITY = 100

  private constructor(
    private readonly value: number,
    private readonly maxVitality: number = Vitality.DEFAULT_MAX_VITALITY
  ) {
    this.validate()
  }

  /**
   * Vitality ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’ç”Ÿæˆã™ã‚‹
   * @param value æ´»åŠ›å€¤
   * @param maxVitality æœ€å¤§æ´»åŠ›å€¤ï¼ˆãƒ‡ãƒ•ã‚©ãƒ«ãƒˆ: 100ï¼‰
   * @throws {Error} ä¸æ­£ãªå€¤ã®å ´åˆ
   */
  static create(value: number, maxVitality: number = Vitality.DEFAULT_MAX_VITALITY): Vitality {
    return new Vitality(value, maxVitality)
  }

  /**
   * å€¤ã®å¦¥å½“æ€§ã‚’æ¤œè¨¼ã™ã‚‹
   * @private
   */
  private validate(): void {
    // å‹ãƒã‚§ãƒƒã‚¯
    if (!isFinite(this.value)) {
      throw new Error('Vitality value must be a finite number')
    }
    if (!isFinite(this.maxVitality)) {
      throw new Error('Maximum vitality must be a finite number')
    }
    
    if (this.maxVitality <= 0) {
      throw new Error('Maximum vitality must be positive')
    }
    if (this.value < 0) {
      throw new Error('Vitality value cannot be negative')
    }
    if (this.value > this.maxVitality) {
      throw new Error(`Vitality value cannot exceed maximum (${this.maxVitality})`)
    }
  }

  /**
   * ç¾åœ¨ã®æ´»åŠ›å€¤ã‚’å–å¾—
   */
  getValue(): number {
    return this.value
  }

  /**
   * æœ€å¤§æ´»åŠ›å€¤ã‚’å–å¾—
   */
  getMax(): number {
    return this.maxVitality
  }

  /**
   * æ´»åŠ›ã‚’æ¸›å°‘ã•ã›ã‚‹
   * @param amount æ¸›å°‘é‡
   * @returns æ–°ã—ã„Vitalityã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹
   * @throws {Error} æ¸›å°‘é‡ãŒè² ã®å ´åˆã¾ãŸã¯ç„¡åŠ¹ãªå€¤ã®å ´åˆ
   */
  decrease(amount: number): Vitality {
    if (typeof amount !== 'number' || !isFinite(amount)) {
      throw new Error('Decrease amount must be a finite number')
    }
    if (amount < 0) {
      throw new Error('Decrease amount must be non-negative')
    }
    return new Vitality(Math.max(0, this.value - amount), this.maxVitality)
  }

  /**
   * æ´»åŠ›ã‚’å¢—åŠ ã•ã›ã‚‹
   * @param amount å¢—åŠ é‡
   * @returns æ–°ã—ã„Vitalityã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹
   * @throws {Error} å¢—åŠ é‡ãŒè² ã®å ´åˆã¾ãŸã¯ç„¡åŠ¹ãªå€¤ã®å ´åˆ
   */
  increase(amount: number): Vitality {
    if (typeof amount !== 'number' || !isFinite(amount)) {
      throw new Error('Increase amount must be a finite number')
    }
    if (amount < 0) {
      throw new Error('Increase amount must be non-negative')
    }
    return new Vitality(Math.min(this.maxVitality, this.value + amount), this.maxVitality)
  }

  /**
   * ãƒ‘ãƒ¼ã‚»ãƒ³ãƒ†ãƒ¼ã‚¸ã‚’å–å¾—ï¼ˆ0-100ï¼‰
   */
  getPercentage(): number {
    return Math.floor((this.value / this.maxVitality) * 100)
  }

  /**
   * æ´»åŠ›ãŒæ¯æ¸‡ã—ã¦ã„ã‚‹ã‹åˆ¤å®š
   */
  isDepleted(): boolean {
    return this.value === 0
  }

  /**
   * æ´»åŠ›ãŒæº€ã‚¿ãƒ³ã‹åˆ¤å®š
   */
  isFull(): boolean {
    return this.value === this.maxVitality
  }

  /**
   * æœ€å¤§æ´»åŠ›å€¤ã‚’å¤‰æ›´ã—ãŸVitalityã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’ä½œæˆ
   * ç¾åœ¨ã®æ´»åŠ›å€¤ãŒæ–°ã—ã„æœ€å¤§å€¤ã‚’è¶…ãˆã‚‹å ´åˆã¯æœ€å¤§å€¤ã«èª¿æ•´
   * @param newMaxVitality æ–°ã—ã„æœ€å¤§æ´»åŠ›å€¤
   * @returns æ–°ã—ã„Vitalityã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹
   */
  withMaxVitality(newMaxVitality: number): Vitality {
    if (typeof newMaxVitality !== 'number' || !isFinite(newMaxVitality)) {
      throw new Error('Maximum vitality must be a finite number')
    }
    const adjustedValue = Math.min(this.value, newMaxVitality)
    return new Vitality(adjustedValue, newMaxVitality)
  }

  /**
   * ä»–ã®Vitalityã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã¨ç­‰ä¾¡ã‹åˆ¤å®š
   */
  equals(other: Vitality): boolean {
    return this.value === other.value && this.maxVitality === other.maxVitality
  }

  /**
   * æ–‡å­—åˆ—è¡¨ç¾ã‚’å–å¾—
   */
  toString(): string {
    return `${this.value}/${this.maxVitality} (${this.getPercentage()}%)`
  }
}