import type { Card } from '../entities/Card'
import type { InsuranceExpirationNotice } from '../types/game.types'

/**
 * ä¿é™ºæœŸé™ç®¡ç†ã‚µãƒ¼ãƒ“ã‚¹
 * 
 * ä¿é™ºã‚«ãƒ¼ãƒ‰ã®æœŸé™åˆ‡ã‚Œå‡¦ç†ã‚’å°‚é–€ã«æ‰±ã†å˜ä¸€è²¬ä»»ã‚¯ãƒ©ã‚¹
 */
export class InsuranceExpirationManager {
  private static readonly EXPIRING_SOON_THRESHOLD = 2

  /**
   * å®šæœŸä¿é™ºã®æœŸé™ã‚’æ›´æ–°ã—ã€æœŸé™åˆ‡ã‚Œã‚’ãƒã‚§ãƒƒã‚¯
   * @param insuranceCards ç¾åœ¨æœ‰åŠ¹ãªä¿é™ºã‚«ãƒ¼ãƒ‰é…åˆ—ï¼ˆå¤‰æ›´ã•ã‚Œã‚‹ï¼‰
   * @param expiredInsurances æœŸé™åˆ‡ã‚Œä¿é™ºã‚«ãƒ¼ãƒ‰é…åˆ—ï¼ˆå¤‰æ›´ã•ã‚Œã‚‹ï¼‰
   * @param currentTurn ç¾åœ¨ã®ã‚¿ãƒ¼ãƒ³æ•°
   * @returns æœŸé™åˆ‡ã‚Œé€šçŸ¥ï¼ˆæœŸé™åˆ‡ã‚ŒãŒãªã„å ´åˆã¯undefinedï¼‰
   */
  updateInsuranceExpirations(
    insuranceCards: Card[], 
    expiredInsurances: Card[], 
    currentTurn: number
  ): InsuranceExpirationNotice | undefined {
    // æœŸé™åˆ‡ã‚Œã«ãªã£ãŸä¿é™ºã‚’ä¸€æ™‚çš„ã«ä¿å­˜
    const nowExpired: Card[] = []
    
    // å…¨ã¦ã®ä¿é™ºã‚«ãƒ¼ãƒ‰ã®æœŸé™ã‚’æ›´æ–°
    insuranceCards.forEach(card => {
      if (card.isTermInsurance()) {
        card.decrementTurn()
        
        // æœŸé™åˆ‡ã‚Œã«ãªã£ãŸã‚‚ã®ã‚’è¨˜éŒ²
        if (card.isExpired()) {
          nowExpired.push(card)
        }
      }
    })
    
    // æœŸé™åˆ‡ã‚Œã®ä¿é™ºã‚’ active ã‹ã‚‰ expired ã«ç§»å‹•
    if (nowExpired.length > 0) {
      // æœŸé™åˆ‡ã‚Œã‚«ãƒ¼ãƒ‰ã‚’ä¿é™ºã‚«ãƒ¼ãƒ‰é…åˆ—ã‹ã‚‰å‰Šé™¤
      nowExpired.forEach(expiredCard => {
        const index = insuranceCards.findIndex(card => card.id === expiredCard.id)
        if (index !== -1) {
          insuranceCards.splice(index, 1)
        }
      })
      
      // æœŸé™åˆ‡ã‚Œé…åˆ—ã«è¿½åŠ 
      expiredInsurances.push(...nowExpired)
      
      // æœŸé™åˆ‡ã‚Œé€šçŸ¥ã‚’ä½œæˆ
      return this.createExpirationNotice(nowExpired, currentTurn)
    }
    
    return undefined
  }

  /**
   * æœŸé™ãŒè¿‘ã„ä¿é™ºã‚«ãƒ¼ãƒ‰ã‚’å–å¾—ï¼ˆæ®‹ã‚ŠæŒ‡å®šã‚¿ãƒ¼ãƒ³ä»¥ä¸‹ï¼‰
   */
  getExpiringSoonInsurances(insuranceCards: Card[]): Card[] {
    return insuranceCards.filter(card => 
      card.isTermInsurance() && 
      card.remainingTurns !== undefined && 
      card.remainingTurns <= InsuranceExpirationManager.EXPIRING_SOON_THRESHOLD && 
      card.remainingTurns > 0
    )
  }

  /**
   * ä¿é™ºæœŸé™åˆ‡ã‚Œã®è­¦å‘Šãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã‚’å–å¾—
   */
  getExpirationWarnings(insuranceCards: Card[]): string[] {
    const expiringSoon = this.getExpiringSoonInsurances(insuranceCards)
    return expiringSoon.map(card => 
      `âš ï¸ ã€Œ${card.name}ã€ã®æœŸé™ã¾ã§ã‚ã¨${card.remainingTurns}ã‚¿ãƒ¼ãƒ³ã§ã™`
    )
  }

  /**
   * æœŸé™åˆ‡ã‚Œé€šçŸ¥ã‚’ä½œæˆ
   * @private
   */
  private createExpirationNotice(expiredCards: Card[], turnNumber: number): InsuranceExpirationNotice {
    const expiredNames = expiredCards.map(card => card.name).join('ã€')
    const message = expiredCards.length === 1 
      ? `å®šæœŸä¿é™ºã€Œ${expiredNames}ã€ã®æœŸé™ãŒåˆ‡ã‚Œã¾ã—ãŸã€‚`
      : `å®šæœŸä¿é™º${expiredCards.length}ä»¶ï¼ˆ${expiredNames}ï¼‰ã®æœŸé™ãŒåˆ‡ã‚Œã¾ã—ãŸã€‚`
    
    return {
      expiredCards,
      message,
      showRenewalOption: true, // å°†æ¥çš„ã«æ›´æ–°ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã‚’å®Ÿè£…ã™ã‚‹ãŸã‚
      turnNumber
    }
  }
}